@import url('https://fonts.googleapis.com/css?family=Quicksand');

.Contact{
    height: 100%; 
     padding-bottom: 12%;
    width: 100%;
  }
.title{
    width: 100%;
      font-size: 65px;
      font-family: 'Dancing Script', cursive;
      margin-top: 25px;
      margin-bottom: 8px;
      font-weight: 600;
      color: rgb(29,63,82);
      text-align: center;
      margin-right: 100px;
}
.msgInfo{
    width: 100%;
   font-size: 20px;
    margin-top: 4px;
    margin-bottom: 15px;
    font-weight: 600;
    color: rgb(29,63,82);
    text-align: center;
    margin-right: 5px;
}
.form{
    width: 450px;
    font-size: 16px;
    /* background: #495C70; */
    padding: 30px 30px 15px 30px;
    /* border: 5px solid #53687E; */
    color: rgb(29,63,82);

    
}
input{
   height: 30px;
   width: 30%;
   margin-bottom: 10px;
   border-radius: 25px;
   padding: 8px 15px;
}

input:focus{
    border-color:  rgb(29,63,82);
}

textarea{
    height: 100px;
   width: 30%;
   margin-bottom: 10px;
   border-radius: 25px; 
   padding: 8px 15px;
}
.send{
    height: 60px;
}
button{
    border: none; 
    background-color:rgb(236,237,243); 
    margin-top: 0;
}
@media (max-width: 915px) {
    .Contact{
        padding-bottom: 20%;
    }
    .title{
        font-size: 50px;
    }

}

@media only screen 
and (min-device-width : 375px) 
and (max-device-width : 667px){
    .Contact{
        padding-bottom: 20%;
    }
    .title{
        font-size: 50px;
    }
}


